.header {
    padding: 15px 2%;
    background: rgba(11,28,36,1);
    position: sticky;
    top: 0;
    overflow: hidden;
}
.logoText {
    color: #fff;
    font-size: 22px;
}
.logoDiv {
    float: left;
    width: 200px;
}
.refreshedDiv {
    float: right;
    width : calc(100% - 250px);
}
.refreshedDiv p {
    float: right;
    color: #fff;
}
.refreshButton {
    float: right;
    margin: 0 0 20px 0;
}